<template>
  <section style="background-color:#fff;">
    <div class="container">
      <!-- Modal -->
      <div
        class="modal fade"
        id="exampleModal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title" id="exampleModalLabel">Player</h5>
              <button type="button" class="close" data-dismiss="modal" v-on:click.prevent="CancelNewPlayer" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <form>
                <div class="form-group">
                  <label for="exampleInputEmail1">Nombre</label>
                  <input type="text" class="form-control" v-model="NewPlayer.Name" />
                </div>

                <div class="form-group">
                  <label for="exampleInputEmail1">GamerTag</label>
                  <input type="text" class="form-control" v-model="NewPlayer.GamerTag" />
                </div>
                <div class="form-group">
                  <label for="exampleDrop">Plataforma</label>
                  <select
                    class="form-control"
                    id="exampleFormControlSelect1"
                    v-model="NewPlayer.Platform"
                  >
                    <option value></option>
                    <option value="battlenet">Battlenet</option>
                    <option value="PS4">PS4</option>
                    <option value="XB1">XB1</option>
                    <option value="ATTV">Activision</option>
                  </select>
                </div>
              </form>
            </div>
            <div class="modal-footer">
              <button type="submit" class="btn btn-primary">Guardar</button>
              <button type="button" class="btn btn-secondary" data-dismiss="modal" v-on:click.prevent="CancelNewPlayer">Cerrar</button>
            </div>
          </div>
        </div>
      </div>
      <!-- Modal ends -->

      <h1>Players</h1>
      <button
        style="float:right;"
        type="button"
        class="btn btn-primary"
        data-toggle="modal"
        data-target="#exampleModal"
      >+ Agregar Player</button>
      <div class="container">
        <div class="row col custyle">
          <table class="table table-striped">
            <thead>
              <tr>
                <th>Nombre</th>
                <th>UserName</th>
                <th>Plataforma</th>
                <th></th>
              </tr>
            </thead>
            <tr v-for="(item, index) in this.Players" v-bind:key="item.playerId">
              <td>{{item.displayName}}</td>
              <td>{{item.platformUserName}}</td>
              <td>
                <span>
                  <i v-if="item.platformName == 'xbl'" class="fab fa-xbox"></i>
                  <i class="fas fa-users fa-stack-1x fa-inverse"></i>
                </span>
                <span>
                  <i v-if="item.platformName == 'battlenet'" class="fab fa-battle-net"></i>
                  <i class="fas fa-users fa-stack-1x fa-inverse"></i>
                </span>
                <span>
                  <i v-if="item.platformName == 'atvi'" class="fas fa-shield-alt"></i>
                  <i class="fas fa-users fa-stack-1x fa-inverse"></i>
                </span>
                <span>
                  <i v-if="item.platformName == 'psn'" class="fab fa-playstation"></i>
                  <i class="fas fa-users fa-stack-1x fa-inverse"></i>
                </span>
                {{item.platformName}}
              </td>
              <td>
                <button
                  class="btn btn-outline-success my-2 my-sm-0"
                  v-on:click.prevent="OpenProfileLink(item);"
                >Profile</button>
              </td>
            </tr>
          </table>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
const axios = require("axios").default;
import Vue from "vue";
import { myVar, Settings } from "@/variables.js";
var moment = require("moment");

export default Vue.extend({
  data() {
    return {
      Players: [],
      NewPlayer: {
        Name: "",
        GamerTag: "",
        Platform: ""
      }
    };
  },
  mounted: function() {
    // this.Authx();
    this.GetPlayers();
  },
  methods: {
    OpenProfileLink(player) {
      var url = "https://cod.tracker.gg/warzone/profile";
      var url2 = "/overview";
      var platform = player.platformName;
      var user = encodeURIComponent(player.platformUserName);

      window.open(url + "/" + platform + "/" + user + url2);
    },
    CancelNewPlayer: function() {
      this.NewPlayer = { Name: "", GamerTag: "", Platform: "" };
    },

    GetPlayers: function() {
      var endpoint = Settings.API + "/players";
      axios
        //.get(endpoint,{headers:{'Access-Control-Allow-Origin': '*' }})
        .get(endpoint)

        .then(response => {
          // handle success
          this.Players = response.data;
        })
        .catch(error => {
          // handle error
          console.log(error);
        })
        .finally(function() {
          // always executed
        });

      //
    }
  }
});
</script>


