<template>
  <div>
    <!-- Portfolio Grid-->
    <section class="page-section bg-light" id="portfolio">
      <div class="container">
        <div class="text-center">
          <h2  class="section-heading text-uppercase">Teams</h2>
          
          <h2 v-if="this.Teams.length == 0 " id="PorConfirmar" class="section-heading text-uppercase" >Por Confirmar</h2>
          <h3 class="section-subheading text-muted">-</h3>
        </div>
        <div class="row">
            



          <div
            v-for="(team, index) in this.Teams" v-bind:key="team.Id"
            class="col-lg-4 col-sm-6 mb-4"
          >
            <div class="portfolio-item">
              <a class="portfolio-link" data-toggle="modal" href="#portfolioModal1">
                <div class="portfolio-hover">
                  <div class="portfolio-hover-content">
                    <i class="fas fa-plus fa-3x"></i>
                  </div>
                </div>
                <img class="img-fluid" :src="team.Logo" alt />
              </a>
              <div class="portfolio-caption">
                <div class="portfolio-caption-heading">{{team.TeamName}}</div>
                <div class="portfolio-caption-subheading text-muted">
                  <ul class="list-inline">
                    <li
                      v-for="(player, index) in team.Players"
                      v-bind:key="player.Id"
                      :class="(player.IsLeader)?'':'list-inline-item'"
                    >
                      <span
                        :class="'badge badge-pill ' + ((player.IsLeader)?'badge-info':'badge-secondary') "
                      >{{player.UserName}}</span>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
// @ is an alias to /src
const { GoogleSpreadsheet } = require("google-spreadsheet");

export default {
  data() {
    return {
      Teams: []
    };
  },

  mounted: function() {
    this.GetTeamsFromGoogleSheet();
  },
  methods: {
    GetTeamsFromGoogleSheet() {
      const doc = new GoogleSpreadsheet(
        "1vcP7_c41xj8eUs5U_GexxZ3IFAj15rJOx_NdZ0iAvhs"
      );
      doc.useApiKey("AIzaSyBw-O1Mu0l1j-KLWU9OsskXbL0qgA9w2Z4");
      var FinalResult = [];
      doc.loadInfo().then(() => {
        //Get the Teams sheet
        const sheet = doc.sheetsByIndex.filter(x => x.title == "Teams")[0]; // or use doc.sheetsById[id]

        //fill the teams array
        sheet.getRows().then(rows => {
          var gsheetrows = [];

          rows.forEach(e => {
              if(e.Confirmado=='si')
              {
            gsheetrows.push({
              TeamName: e.Team,
              Player: e.Player,
              IsLeader: e.Capitan == "si",
              Logo: e.Logo
            });
              }
          });

          //---------Normalize Data------------

          var cnter = 1;

          //Get Distinct teams
          const teams = [...new Set(gsheetrows.map(item => item.TeamName))];
          var cnter = 1;
          //Push the teams to the final Object
          teams.forEach(team => {
            FinalResult.push({ TeamName: team, Id: cnter, Players: [] });
            cnter++;
          });

          FinalResult.forEach(FR => {
            //Get rows matching team
            var rws = gsheetrows.filter(x => x.TeamName == FR.TeamName);

            FR.Logo = rws[0].Logo;
            var cntr2 = 1;
            rws.forEach(p => {
              FR.Players.push({
                UserName: p.Player,
                IsLeader: p.IsLeader,
                Id: FR.Id + "-" + cntr2
              });
              cntr2++;
            });
          });
        });

        this.Teams = FinalResult;
      });
    }
  }
};
</script>
<style scoped>

#PorConfirmar
{
    top: 50px;
    position: relative;
    color: #fed137;
}
</style>

