<template>
  <section class="page-section" style="background-color:#fff;" id="reglas">
    <div class="container">
      <div class="row">
        <div class="col">
          <h1>Seleccion de partidas</h1>
        </div>
      </div>
    <div class="row">
          <div class="col-md-4">
                   <h3>Team:</h3> <select class="form-control" @change="SelectEvent($event)">
               <option :value="null"></option>
  <option   v-for="(item, index) in this.Teams" v-bind:key="item.Id"  v-bind:value="item.Id">    {{item.Name}}</option>

</select>
        </div>
        
        </div>


    </div>
<div class="container main-section">
		<div class="row">
			<div class="col-lg-12 hedding pb-2">
				
			</div>
			<div class="col-lg-12">
				<table class="table table-bordered" style="border-collapse:collapse;">
				    <thead>
				        <tr>
				            <th>Detalles</th>
				            <th>Id</th>
				            <th>Hora</th>
				            <th>Duracion</th>
				            <th>Kills</th>
				            <th>Posicion</th>
                            <th>Status</th>
                            <th>Seleccion</th>
				        </tr>
				    </thead>
				    <tbody v-for="(item, index) in this.TeamMatches.Matches" v-bind:key="item.Id">
			    		<tr colspan="6" >
				            <td><button data-toggle="collapse" v-bind:data-target="'#demo'+index"  class="btn btn-outline-success accordion-toggle" style="font-size: 25px;"><i  class="fas fa-caret-square-down"></i></button> </td>
				                          <td>{{item.Id}}</td>
              <td>{{item.MatchStart}}</td>
              <td>{{item.Duration}}</td>
              <td>{{ GetTotalKills(item.Players)}}</td>
              <td>{{item.Placement}}</td>
              <td>{{ (item.Selected)?"Seleccionada":"Descartada"  }}</td>

              <td>
                <button v-if="!item.Selected"
                  class="btn btn-outline-success my-2 my-sm-0"
                  
                  v-on:click.prevent="Test(item.Id,true)"
                >Seleccionar</button>

                <button v-if="item.Selected"
                  class="btn btn-outline-danger my-2 my-sm-0"
                  
                  v-on:click.prevent="Test(item.Id,false)"
                >DescartAR</button>


              </td>
				        </tr>
				        <tr class="p">
				            <td colspan="8" class="hiddenRow">
					            <div class="accordian-body collapse p-3" v-bind:id="'demo' + index">
                                    
<table class="table table-bordered" style="border-collapse:collapse; color:#fff;">
				    <thead>
				        <tr>
				            <th>Player</th>
                            <th>Kills</th>

				        </tr>
				    </thead>
                    <tbody><tr v-for="(player, index) in item.Players" v-bind:key="player.UserName">
                        <td>{{player.UserName}}</td>
                        <td>{{player.Kills}}</td>
                        </tr>
                    </tbody>

</table>










					        	</div> 
				        	</td> 
				        </tr>		
				    </tbody>
                    
				</table>
			</div>
		</div>
	</div>








  </section>
</template>

<script>
const axios = require("axios").default;
import Vue from "vue";
import { myVar, Settings } from "@/variables.js";

export default Vue.extend({
  data() {
    return {
      Teams: [],
      TeamMatches:[],
      SelectedItem:-1,
      SelectedText:""
    };
  },
  mounted: function() {
   //this.Authx();
    this.GetTeams();



   
  },
  methods: {

      Authx: function()
      {
        var decrypted = CryptoJS.AES.decrypt(Settings.pt, "ZUVvShFSES2");
        var pass = prompt("pass");
        if(decrypted.toString(CryptoJS.enc.Utf8) != pass)
        {
                this.$router.push('/');
                dfdf
        }


      },


      GetTotalKills: function(players)
      {var totalKills = 0;
          players.forEach(element => {
              totalKills += element.Kills;
          });
          return totalKills;

      },
SelectEvent : function(event){

    if(!event.target.value){return false;}
   
    this.SelectedItem = event.target.value;
    this.SelectedText = event.target.selectedOptions[0].text;
this.GetTeamMatches(event.target.value);


},

    Test: function(MatchId,action) {
      var r = confirm(((action)? "Seleccionar  " : "Descartar ") + MatchId +" para " + this.SelectedText + "?");
      
      if (!r) {
        return;
      }

     this.UpdateTeamMatch(this.SelectedItem , MatchId);

    },

    UpdateTeamMatch(teamId,MatchId) {
      var endpoint = Settings.API + "/UpdateMatch";
      axios
        .post(endpoint, {
          TeamId: teamId,
          MatchId: MatchId,
          Selected:action
        })
        .then(function(response) {
         //Successfull
         alert( "Actualizado" );

      this.GetTeamMatches();


        })
        .catch(function(error) {
           alert( "Error" );

        });
    },
    GetTeams: function() {
      var endpoint = "/tempData/Teams.json";
      axios
        .get(endpoint)
        .then(response => {
          // handle success
          this.Teams = response.data;
          
        })
        .catch(error => {
          // handle error
          console.log(error);
        })
        .finally(function() {
          // always executed
        });

      //
    },

    GetTeamMatches: function(TeamId) {
      var endpoint = "/tempData/TeamMatches.json";
      axios
        .get(endpoint)
        .then(response => {
          // handle success
          this.TeamMatches = response.data;
        
        
        })
        .catch(error => {
          // handle error
          console.log(error);
        })
        .finally(function() {
          // always executed
        });

      //
    }


  }
});
</script>
<style scoped>

.hiddenRow {
    padding: 0 4px !important;
    background-color: #28a744;
    font-size: 13px;
    color:#fff;
}
.accordian-body span{
	color:#a2a2a2 !important;
}


</style>

