import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Stats from '../views/Stats.vue'
import TeamStart from '../views/TeamStart.vue'
import Matches from '../views/Matches.vue'
import Results from '../views/Results.vue'
import Results2 from '../views/Results2.vue'
import Players from '../views/Players.vue'
import Tournaments from '../views/Tournaments.vue' 

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/stats',
    name: 'Stats',
    component: Stats
  },
  {
    path: '/teamstart',
    name: 'teamStart',
    component: TeamStart
  },
  {
    path: '/matches',
    name: 'Matches',
    component: Matches
  },
  {
    path: '/results',
    name: 'Results',
    component: Results
  },
  {
    path: '/results2',
    name: 'Results2',
    component: Results2
  },
  {
    path: '/players',
    name: 'Players',
    component: Players
  },
  {
    path: '/tournaments',
    name: 'Tournaments',
    component: Tournaments
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
