        <div class="col text-right" > <h4>Score: <span class="badge badge-success">{{this.TeamScore}}</span></h4> </div>
        <div class="col text-right" > <h4>Total de Kills: <span class="badge badge-secondary"> {{this.SelectedTeam.TotalKills}}</span></h4> </div>
        </div>
    </div>
    

<div class="container main-section">
		<div class="row">
			<div class="col-lg-12 hedding pb-2">
				
			</div>
			<div class="col-md-12">
				<table class="table table-bordered" style="border-collapse:collapse;">
				    <thead>
				        <tr style="font-size:10px">
				            <th>Detalles</th>
				            <th>Kills</th>
				            <th>Posicion</th>
                            <th>Puntos-Kills</th>
                            <th>Puntos-Posicion</th>
                            <th>Puntos Total</th>
				        </tr>
				    </thead>
				    <tbody v-for="(item, index) in this.TeamMatches" v-bind:key="item.Id">
			    		<tr colspan="6" >
				            <td><button data-toggle="collapse" v-bind:data-target="'#demo'+index"  class="btn btn-outline-warning accordion-toggle" style="font-size: 25px;"><i  class="fas fa-caret-square-down"></i></button> </td>
				                         
              <td>{{ GetTotalKills(item.Players)}}</td>
