  <li class=""><span>{{item.MatchStart}}</span></li>
  <li class="list-inline-item"><span >{{item.Duration}}</span></li>
</ul>
                                    
<table class="table table-bordered" style="border-collapse:collapse; color:#fff;">
				    <thead>
