</table>
					        	</div> 



  <br />
<div class="container">
<!-- Button trigger modal -->
<button type="button" class="btn btn-primary" data-toggle="modal" data-target="#exampleModal">
  Como se calcula la informacion?
</button>

<!-- Modal -->
<div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog" role="document">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title" id="exampleModalLabel">Puntos</h5>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div class="modal-body">
        <h4>Puntos por Kill: {{LocalSettings.pointsPerKill}}</h4>
        <table class="table table-bordered table table-sm" style="">
				    <thead>
				        <tr>
				            <th>Posicion</th>
                            <th>Puntos</th>

				        </tr>
				    </thead>
                    <tbody><tr v-for="(p, index) in LocalSettings.pointsPerPossition" v-bind:key="p.Position">
                        <td :v-if="index > 0" >{{p.Position}}</td>
                        <td :v-if="index > 0">{{p.Points}}</td>
                        </tr>
                    </tbody>

</table>
        
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-dismiss="modal">Cerrar</button>
      </div>
    </div>
