</template>

<script>
const axios = require("axios").default;
import Vue from "vue";
import { myVar, Settings } from "@/variables.js";

export default Vue.extend({
  data() {
    return {
      FinalResults:[],
      Teams: [],
      TeamMatches:[],
      SelectedItem:-1,
      SelectedText:"",
      ChartData:[['Sleepless Soldiers', 450], ['Ate-Gaming', 320]],
      ChartDataColors:['#820606', '#b500ff'],
      score:30,
      barColors:["bg-info","bg-success","bg-warning","bg-danger"],
      TeamScore:0 ,
      SelectedTeam:{},
      TopKillers:[],
      LocalSettings: Settings

    };
  },
  mounted: function() {
   //this.Authx();
    this.GetFinalResults();



   
  },
  methods: {


      SetTeamLeader()
      {
         var maxPoints = 0;
        this.FinalResults.forEach(t => {
           maxPoints  = (t.TeamTotalPoints> maxPoints) ? t.TeamTotalPoints: maxPoints;
        });


          //Set the Is Leader indicator
          this.FinalResults.forEach(t => {
          
          t.IsLeader =(t.TeamTotalPoints == maxPoints);
          
          
          });

          //Calculate the top 10

          this.FinalResults.forEach(team => {
           
              team.PlayerStats.forEach(ps => {
                
                this.TopKillers.push({UserName:"["+team.TeamName +"]"+ ps.UserName,Kills:ps.Kills,TeamTotalPoints:team.TeamTotalPoints});
              });
              

          });

          this.TopKillers = this.TopKillers.sort(this.ArraySorterTops);
           


          

         

      },

      CalculateTeamScore(team)
      {
        team.TeamTotalPoints = 0;
        team.MatchesPlayed = team.Matches.filter(x=> x.Selected).length;
        team.Matches.filter(x=> x.Selected).forEach(match => {
        var ppp = Settings.pointsPerPossition.filter(x=> x.Position == match.Placement)
        match.PointsPerPoss = (ppp.length > 0)? ppp[0].Points:0;
          match.TotalKills = 0;
          match.PointsPerKills = 0;
          match.Players.forEach(player => {
              match.TotalKills += player.Kills;
          });
          match.PointsPerKills = (match.TotalKills * Settings.pointsPerKill);
          match.TotalMatchPoints = match.PointsPerKills + match.PointsPerPoss;
          team.TeamTotalPoints += match.TotalMatchPoints
      
        });

          team.ProgressBar = ((team.TeamTotalPoints * 100) /Settings.MaxPointsInProgressBar);
        //HighLights
        const reducer = (accumulator, elem) => accumulator + elem.TotalKills;
        var accum = 0;
         team.TotalKills = team.Matches.filter(x=> x.Selected).reduce(reducer,accum);
        team.PlayerStats=[]
        team.Matches.filter(x=> x.Selected).forEach(match => {
            match.Players.forEach(p => {

              var pstat = team.PlayerStats.filter(x=> x.UserName == p.UserName);
              
                 if(pstat.length == 0 )
                 {
                   
                   team.PlayerStats.push({UserName:p.UserName, Kills:p.Kills});
                 }
                 else
                 {
                   
                    pstat[0].Kills+= p.Kills;

                 }
            });
        });
        team.PlayerStats = team.PlayerStats.sort(this.ArraySorter);




      },

     ArraySorter( a, b ) {
  if ( a.Kills > b.Kills ){
    return -1;
  }
  if ( a.Kills < b.Kills ){
    return 1;
  }
  return 0;
},

     ArraySorterTops( a, b ) {
  if ( a.Kills > b.Kills ){
    return -1;
  }
  if ( a.Kills < b.Kills ){
    return 1;
  }
  if(a.TeamTotalPoints > b.TeamTotalPoints)
  {
    
    return -1;
  }
  return 0;
},


      GetColor:function (idx)
      {

          var op = Math.floor(idx/this.barColors.length);
         
         if(op == 0)
         {
              
             return this.barColors[idx];
         }
         else
         {  var selectedc = (idx - (this.barColors.length * op));
              
             return this.barColors[ selectedc ];
         }


      },

      Authx: function()
      {
        var decrypted = CryptoJS.AES.decrypt(Settings.pt, "ZUVvShFSES2");
        var pass = prompt("pass");
        if(decrypted.toString(CryptoJS.enc.Utf8) != pass)
        {
                this.$router.push('/');
                dfdf
        }


      },


      GetTotalKills: function(players)
      {var totalKills = 0;
          players.forEach(element => {
              totalKills += element.Kills;
          });
          return totalKills;

      },
SelectEvent : function(event){

    if(!event.target.value){return false;}
   
    this.SelectedItem = event.target.value;
    this.SelectedText = event.target.selectedOptions[0].text;
    this.SelectedTeam  = this.FinalResults.filter(x=> x.Id == this.SelectedItem)[0];
    
    
this.GetTeamMatches();




},

    Test: function(MatchId,action) {
      var r = confirm(((action)? "Seleccionar  " : "Descartar ") + MatchId +" para " + this.SelectedText + "?");
      
      if (!r) {
        return;
      }

     this.UpdateTeamMatch(this.SelectedItem , MatchId);

    },

    UpdateTeamMatch(teamId,MatchId) {
      var endpoint = Settings.API + "/UpdateMatch";
      axios
        .post(endpoint, {
          TeamId: teamId,
          MatchId: MatchId,
          Selected:action
        })
        .then(function(response) {
         //Successfull
         alert( "Actualizado" );

      this.GetTeamMatches();


        })
        .catch(function(error) {
           alert( "Error" );

        });
    },
    GetFinalResults: function() {
      var rand = Math.floor(Math.random() * 33343);
      var endpoint = "/tempData/Results.json?" + "rand=" + rand;
      axios
        .get(endpoint)
        .then(response => {
          // handle success

          this.FinalResults = response.data;
          this.FinalResults.forEach(team => {
            this.CalculateTeamScore(team);
            
          });
          this.SetTeamLeader();
          
        })
        .catch(error => {
          // handle error
          console.log(error);
        })
        .finally(function() {
          // always executed
        });

      //
    },

    GetTeamMatches: function() {
      
      
          var tm = this.FinalResults.filter(x=> x.Id == this.SelectedTeam.Id)[0]
      
          this.TeamScore = tm.TeamTotalPoints;
          this.TeamMatches = (tm == undefined)?{}:tm.Matches.filter(x=> x.Selected);
     
    }


  }
});
</script>
