        
        
<template>

        <!-- Team-->
        <section class="page-section" id="team">
            <div class="container">
                <div class="text-center">
                    <h2 class="section-heading text-uppercase">Streamers officiales</h2>
                    <h3 class="section-subheading text-muted">Estaran transmitiendo en vivo las partidas de sus equipos asi como tambien daran informacion en general del torneo.</h3>
                </div>
                <div class="row justify-content-md-center">




                    <div v-for="(streamer, index) in this.Streamers" v-bind:key="streamer.Nombre" class="col-lg-4">
                        <div class="team-member">
                            <img class="mx-auto rounded-circle" :src="streamer.Logo" alt="" />
                            <h4>{{streamer.Nombre}}</h4>
                            <p class="text-muted">{{streamer.Slogan}}</p>

                            <a v-for="(lnk, index) in streamer.Links" v-bind:key="lnk.tipo" class="btn btn-dark btn-social mx-2" href="#" :onclick="'window.open(\'' + lnk.url +'\');'" ><i :class="'fab '+ ((lnk.tipo == 'twitch')?'fa-twitch':'fa-youtube') "></i></a>
                        </div>
                    </div>

                </div>
                <div class="row">
                    <div class="col-lg-8 mx-auto text-center"><p class="large text-muted">Dales un follow y compartelo con tus amigos.</p></div>
                </div>
            </div>
        </section>

</template>

<script>

// @ is an alias to /src
import Teams from '@/components/Teams.vue'
import Streamers from '@/components/Streamers.vue'
const { GoogleSpreadsheet } = require("google-spreadsheet");



export default {
  components: {
    Teams,
    Streamers
  },
    data() {
    return {
      Streamers: {}
    };
  },

  mounted: function(){

      this.GetTextFromGoogleSheet();


  },

  methods:
  {
      GetTextFromGoogleSheet:async function()
      {
      const doc = new GoogleSpreadsheet(
        "1vcP7_c41xj8eUs5U_GexxZ3IFAj15rJOx_NdZ0iAvhs"
      );
      doc.useApiKey("AIzaSyBw-O1Mu0l1j-KLWU9OsskXbL0qgA9w2Z4");
      var FinalResult = [];
      doc.loadInfo().then(() => {
        //Get the Teams sheet
        const sheet = doc.sheetsByIndex.filter(x => x.title == "Streamers")[0]; // or use doc.sheetsById[id]

        //fill the teams array
        sheet.getRows().then(rows => {
           this.Streamers = JSON.parse(rows[0].Streamers);
        });

      });


      }




  }
}

</script>

<style scoped>
h4{

    color:#fff;
}
</style>
