<template>

  <section class="page-section" id="reglas">

  <div class="container">
  <div class="row">
          <div class="col-md-4">
             <form class="form-inline my-2 my-lg-0">
          <input class="form-control mr-sm-2" type="text" placeholder="usuario#id" aria-label="usuario#id" v-model="userTag">
          <button class="btn btn-outline-success my-2 my-sm-0" v-on:click.prevent="Test">buscar</button>
        </form>
          </div>
  </div>



<div class="container">
    <div class="row col-md-6 col-md-offset-2 custyle">
    <table class="table table-striped custab">
    <thead>
        <tr>
            <th>ID</th>
            <th>Fecha</th>
            <th>Tipo</th>
            <th>Duracion</th>
            <th>Puesto</th>
            <th>Kills</th>
            
        </tr>
    </thead>
            <tr v-for="(item, index) in matches" v-bind:key="item.id">
                <td>{{index}}</td>
