</template>

<script>

const axios = require('axios').default;


export default {
  name: 'HelloWorld',
  data: function () {
    return {
      userTag: 'davidrf88#1741',
     // matches: [{attributes:{id:'asdfsadf'}},{attributes:{id:'asdffsadf'}}]
     matches:[]
    }
  },
  methods: {
  Test: function () {

 var tagEncoded= encodeURIComponent(this.userTag);
 var endpoint = 'https://cors-anywhere.herokuapp.com/https://api.tracker.gg/api/v1/warzone/matches/battlenet/'+tagEncoded+'?type=wz&next=null'
axios.get(endpoint)
  .then((response) => {
    // handle success
    console.log(this.matches)
    this.matches = response.data.data.matches;
  })
  .catch((error) => {
    // handle error
    console.log(error);
  })
  .finally(function () {
    // always executed
  });

  //  

  }
}






}
</script>
