<template>
  <section class="page-section" style="background-color:#fff;">
    <div class="container">
      <br />
      <div class="row justify-content-between">
        <div class="col-4">
          <h1>Resultados</h1>
        </div>
        <div class="col-4">
          <select class="form-control" @change="SelectResults($event)">
            <option
              v-for="(item, index) in this.ResultsHeader"
              v-bind:key="item"
              v-bind:value="item"
            >{{item}}</option>
          </select>
        </div>
      </div>

      <div v-for="(team, index) in this.FinalResults" v-bind:key="team.Id">
        <div class="progress" style="height:50px;">
          <div
            v-bind:class="'progress-bar progress-bar-striped '+ GetColor(index) "
            role="progressbar"
            v-bind:style="'width: '+ team.ProgressBar  +'%;'"
            aria-valuenow="10"
            aria-valuemin="0"
            aria-valuemax="100"
          ></div>
          <div class="justify-content-start d-flex align-self-center position-absolute">
            <span class="ProgressText">
              {{team.TeamName}} ({{team.MatchesPlayed}}/{{LocalSettings.TotalMatches}}  ) - {{team.TeamTotalPoints}}
              <span v-if="team.isleader"> <i style=" font-size:20px;"  class="fas fa-star"></i> </span>
              
               
            </span>
          </div>
        </div>
        <br />
      </div>
    </div>

    <br />
    <br />
    <div class="container TOP KILLERS">
      <!-- Button trigger modal -->
      <button
        type="button"
        class="btn btn-primary"
        data-toggle="modal"
        data-target="#exampleModalKillers"
      >Top Killers</button>

      <!-- Modal -->
      <div
        class="modal fade"
        id="exampleModalKillers"
        tabindex="-1"
        role="dialog"
        aria-labelledby="exampleModalKillersLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <table class="table table-bordered table table-sm" style>
                <thead>
                  <tr>
                    <th>#</th>
                    <th>Player</th>
                    <th>Kills</th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    v-for="(p, index) in this.TopKillers.filter((w,idx,arr) => idx <= this.LocalSettings.MaxTopKillers)"
                    v-bind:key="p.UserName"
                  >
                    <td>{{index}}</td>
                    <td>{{p.UserName}}</td>
                    <td>{{p.Kills}}</td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-secondary" data-dismiss="modal">Cerrar</button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <br />
    <br />
    <br />

    <div class="container">
      <div class="row">
        <div class="col">
          <h3>Detalles</h3>
        </div>
      </div>
      <div class="row">
        <div class="col-md-6">
          <h4>Team:</h4>
          <select class="form-control" @change="SelectEvent($event)" v-model="selectedT" >
            <option :value="null"></option>
            <option
              v-for="(item, index) in this.FinalResults"
              v-bind:key="item.Id"
              v-bind:value="item.Id"
            >{{item.TeamName}}</option>
          </select>
        </div>
        <div class="col text-right">
          <h4>
            Score:
            <span class="badge badge-success">{{this.TeamScore}}</span>
          </h4>
        </div>
        <div class="col text-right">
          <h4>
            Total de Kills:
            <span class="badge badge-secondary">{{this.SelectedTeam.TotalKills}}</span>
          </h4>
        </div>
      </div>
    </div>

    <div class="container main-section">
      <div class="row">
        <div class="col-lg-12 hedding pb-2"></div>
        <div class="col-md-12">
          <table class="table table-bordered" style="border-collapse:collapse;">
            <thead>
              <tr style="font-size:10px">
                <th>Detalles</th>
                <th>Kills</th>
                <th>Posicion</th>
                <th>Puntos-Kills</th>
                <th>Puntos-Posicion</th>
                <th>Puntos Total</th>
              </tr>
            </thead>
            <tbody v-for="(item, index) in this.TeamMatches" v-bind:key="item.Id">
              <tr colspan="6">
                <td>
                  <button
                    data-toggle="collapse"
                    v-bind:data-target="'#demo'+index"
                    class="btn btn-outline-warning accordion-toggle"
                    style="font-size: 25px;"
                  >
                    <i class="fas fa-caret-square-down"></i>
                  </button>
                </td>

                <td>{{ GetTotalKills(item.Players)}}</td>
                <td>{{item.Placement}}</td>
                <td>{{item.PointsPerKills}}</td>
                <td>{{item.PointsPerPoss}}</td>
                <td>{{item.TotalMatchPoints}}</td>
              </tr>
              <tr class="p">
                <td colspan="6" class="hiddenRow">
                  <div class="accordian-body collapse p-3" v-bind:id="'demo' + index">
                    <ul class="list-inline">
                      <li class>
                        <span>{{item.MatchStart}}</span>
                      </li>
                      <li class="list-inline-item">
                        <span>{{item.Duration}}</span>
                      </li>
                    </ul>

                    <table
                      class="table table-bordered"
                      style="border-collapse:collapse; color:#fff;"
                    >
                      <thead>
                        <tr>
                          <th>Player</th>
                          <th>Kills</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="(player, index) in item.Players" v-bind:key="player.UserName">
                          <td>{{player.UserName}}</td>
                          <td>{{player.Kills}}</td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
    <br />
    <div class="container">
      <table class="table table-bordered">
        <thead>
          <tr>
            <th>Player</th>
            <th>Kills totales</th>
          </tr>
        </thead>
        <tbody>
          <tr
            v-for="(player, indexx) in this.SelectedTeam.PlayerStats"
            v-bind:key="player.UserName"
          >
            <td>{{player.UserName}}</td>
            <td>{{player.Kills}}</td>
          </tr>
        </tbody>
      </table>
    </div>

    <br />
    <div class="container">
      <!-- Button trigger modal -->
      <button
        type="button"
        class="btn btn-primary"
        data-toggle="modal"
        data-target="#exampleModal"
      >Como se calcula la informacion?</button>

      <!-- Modal -->
      <div
        class="modal fade"
        id="exampleModal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content">
            <div class="modal-header">
              <h5 class="modal-title" id="exampleModalLabel">Puntos</h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <h4>Puntos por Kill: {{LocalSettings.pointsPerKill}}</h4>
              <table class="table table-bordered table table-sm" style>
                <thead>
                  <tr>
                    <th>Posicion</th>
                    <th>Puntos</th>
                  </tr>
                </thead>
                <tbody>
                  <tr
                    v-for="(p, index) in LocalSettings.pointsPerPossition"
                    v-bind:key="p.Position"
                  >
                    <td :v-if="index > 0">{{p.Position}}</td>
                    <td :v-if="index > 0">{{p.Points}}</td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div class="modal-footer">
              <button type="button" class="btn btn-secondary" data-dismiss="modal">Cerrar</button>
            </div>
          </div>
        </div>
      </div>
    </div>

    <br />
    <br />
    <br />
  </section>
</template>

<script>
const axios = require("axios").default;
import Vue from "vue";
import { myVar, Settings } from "@/variables.js";
const { GoogleSpreadsheet } = require("google-spreadsheet");

export default Vue.extend({
  data() {
    return {
      FinalResults: [],
      Teams: [],
      TeamMatches: [],
      SelectedItem: -1,
      SelectedText: "",
      ChartData: [
        ["Sleepless Soldiers", 450],
        ["Ate-Gaming", 320]
      ],
      ChartDataColors: ["#820606", "#b500ff"],
      score: 30,
      barColors: ["bg-info", "bg-success", "bg-warning", "bg-danger"],
      TeamScore: 0,
      SelectedTeam: {},
      TopKillers: [],
      ResultsHeader: [],
      TeamLeader:'',
      selectedT:null,
      LocalSettings:{} //Settings
    };
  },
  mounted: function() {
    //this.Authx();
    // this.GetFinalResults();

          this.GetResultsHeaders().then(() => {

         
          this.GetGoogleSheetResults(this.ResultsHeader[0]);
    })

 

    
  },
  methods: {

    SetTeamLeader: function() {
      var maxPoints = 0;
      this.FinalResults.forEach(t => {
        maxPoints =
          t.TeamTotalPoints > maxPoints ? t.TeamTotalPoints : maxPoints;
      });

      //Set the Is Leader indicator
      this.FinalResults.forEach(t => {
      
        if(t.TeamTotalPoints == maxPoints){
        t.isleader = true;
        this.TeamLeader = t.TeamName 
        }

      });

      

      //Calculate the top 10
this.TopKillers = [];
      this.FinalResults.forEach(team => {
        team.PlayerStats.forEach(ps => {
          this.TopKillers.push({
            UserName: "[" + team.TeamName + "]" + ps.UserName,
            Kills: ps.Kills,
            TeamTotalPoints: team.TeamTotalPoints
          });
        });
      });

      this.TopKillers = this.TopKillers.sort(this.ArraySorterTops);
    },

    CalculateTeamScore: function(team) {
      team.TeamTotalPoints = 0;
      team.MatchesPlayed = team.Matches.filter(x => x.Selected).length;
      team.Matches.filter(x => x.Selected).forEach(match => {
        var ppp = this.LocalSettings.pointsPerPossition.filter(
          x => x.Position == match.Placement
        );
        match.PointsPerPoss = ppp.length > 0 ? ppp[0].Points : 0;
        //  console.log('PointsPerPoss',match.PointsPerPoss)
        match.TotalKills = 0;
        //  console.log('TotalKills',match.TotalKills)
        match.PointsPerKills = 0;
        match.Players.forEach(player => {
          match.TotalKills += player.Kills;
          //  console.log('player.Kills', player.Kills)
          //  console.log('+match.TotalKills', match.TotalKills)
        });
        match.PointsPerKills = match.TotalKills * this.LocalSettings.pointsPerKill;
        //console.log('PointsPerKills',match.PointsPerKills)
        match.TotalMatchPoints = match.PointsPerKills + match.PointsPerPoss;
        // console.log('TotalMatchPoints',match.TotalMatchPoints)
        team.TeamTotalPoints += match.TotalMatchPoints;
      });

      team.ProgressBar =
        (team.TeamTotalPoints * 100) / this.LocalSettings.MaxPointsInProgressBar;
      //HighLights
      const reducer = (accumulator, elem) => accumulator + elem.TotalKills;
      var accum = 0;
      team.TotalKills = team.Matches.filter(x => x.Selected).reduce(
        reducer,
        accum
      );
      team.PlayerStats = [];
      team.Matches.filter(x => x.Selected).forEach(match => {
        match.Players.forEach(p => {
          var pstat = team.PlayerStats.filter(x => x.UserName == p.UserName);

          if (pstat.length == 0) {
            team.PlayerStats.push({
              UserName: p.UserName,
              Kills: Number(p.Kills)
            });
          } else {
            pstat[0].Kills += p.Kills;
          }
        });
      });
      team.PlayerStats = team.PlayerStats.sort(this.ArraySorter);
    },

    ArraySorter(a, b) {
      if (a.Kills > b.Kills) {
        return -1;
      }
      if (a.Kills < b.Kills) {
        return 1;
      }
      return 0;
    },

    ArraySorterTops(a, b) {
      if (a.Kills > b.Kills) {
        return -1;
      }
      if (a.Kills < b.Kills) {
        return 1;
      }
      if (a.TeamTotalPoints > b.TeamTotalPoints) {
        return -1;
      }
      return 0;
    },

    GetColor: function(idx) {
      var op = Math.floor(idx / this.barColors.length);

      if (op == 0) {
        return this.barColors[idx];
      } else {
        var selectedc = idx - this.barColors.length * op;

        return this.barColors[selectedc];
      }
    },

    Authx: function() {
      var decrypted = CryptoJS.AES.decrypt(LocalSettings.pt, "ZUVvShFSES2");
      var pass = prompt("pass");
      if (decrypted.toString(CryptoJS.enc.Utf8) != pass) {
        this.$router.push("/");
        dfdf;
      }
    },

    GetTotalKills: function(players) {
      var totalKills = 0;
      players.forEach(element => {
        totalKills += element.Kills;
      });
      return totalKills;
    },
    SelectEvent: function(event) {
      //console.log('selected',  event.target.value)
      if (!event.target.value) {
        return false;
      }

      this.SelectedItem = event.target.value;
      this.SelectedText = event.target.selectedOptions[0].text;
      this.SelectedTeam = this.FinalResults.filter(
        x => x.Id == this.SelectedItem
      )[0];

      this.GetTeamMatches();
    },

    SelectResults: function(event){
if (!event.target.value) {
        return false;
      }
        this.GetGoogleSheetResults(event.target.value);

    },

    Test: function(MatchId, action) {
      var r = confirm(
        (action ? "Seleccionar  " : "Descartar ") +
          MatchId +
          " para " +
          this.SelectedText +
          "?"
      );

      if (!r) {
        return;
      }

      this.UpdateTeamMatch(this.SelectedItem, MatchId);
    },

    UpdateTeamMatch(teamId, MatchId) {
      var endpoint = LocalSettings.API + "/UpdateMatch";
      axios
        .post(endpoint, {
          TeamId: teamId,
          MatchId: MatchId,
          Selected: action
        })
        .then(function(response) {
          //Successfull
          alert("Actualizado");

          this.GetTeamMatches();
        })
        .catch(function(error) {
          alert("Error");
        });
    },
    GetFinalResults: function() {
      var rand = Math.floor(Math.random() * 33343);
      var endpoint = "/tempData/Results.json?" + "rand=" + rand;
      axios
        .get(endpoint)
        .then(response => {
          // handle success

          this.FinalResults = response.data;
          this.FinalResults.forEach(team => {
            this.CalculateTeamScore(team);
          });
          this.SetTeamLeader();
        })
        .catch(error => {
          // handle error
          console.log(error);
        })
        .finally(function() {
          // always executed
        });

      //
    },

    GetResultsHeaders: async function() {
      
      const doc = new GoogleSpreadsheet(
        "1vcP7_c41xj8eUs5U_GexxZ3IFAj15rJOx_NdZ0iAvhs"
      );
      doc.useApiKey("AIzaSyBw-O1Mu0l1j-KLWU9OsskXbL0qgA9w2Z4");

      await doc.loadInfo().then(() => {
        doc.sheetsByIndex.forEach(sheet => {
          if (sheet.title.startsWith("Resultados-")) {
            this.ResultsHeader.push(sheet.title);
            
          // console.log('load' + sheet.title);
          }
        });
      });
    },

    CleanUp:function(){
      this.TeamScore=0;
      this.selectedT=null;
      this.TeamMatches= [];
      this.SelectedItem=-1;
      this.SelectedText="",
      this.SelectedTeam={};
      this.TopKillers=[];


    },

    GetGoogleSheetResults: function(sheetName) {
      this.CleanUp();
      //console.log('loading: ', sheetName);
      const doc = new GoogleSpreadsheet(
        "1vcP7_c41xj8eUs5U_GexxZ3IFAj15rJOx_NdZ0iAvhs"
      );
      doc.useApiKey("AIzaSyBw-O1Mu0l1j-KLWU9OsskXbL0qgA9w2Z4");

      doc.loadInfo().then(() => {
         
        const sheet = doc.sheetsByIndex.filter(x=> x.title == sheetName)[0]; // or use doc.sheetsById[id]

        sheet.getRows().then(rows => {
          
          //Get the tournament variables from the first row
          this.SetVariables(rows[0].Reglas);

          var Stats = [];

          rows.forEach(e => {
            Stats.push({
              Selected: e.Cuenta,
              Team: e.Team,
              Partida: e.Partida,
              Jugador: e.Jugador,
              Kills: Number(e.Kills),
              Posicion: e.Posicion
            });
          });

          var NormalizedResults = this.NormalizeGoogleSheetResults(Stats);

          this.FinalResults = NormalizedResults;
          this.FinalResults.forEach(team => {
            this.CalculateTeamScore(team);
          });
          this.SetTeamLeader();
        });
      });
    },

    NormalizeGoogleSheetResults: function(gsheetrows) {
      var FinalResult = [];
      const teams = [...new Set(gsheetrows.map(item => item.Team))];
      var cnter = 1;
      teams.forEach(team => {
        FinalResult.push({ TeamName: team, Matches: [], Id: cnter });
        cnter++;
      });

      FinalResult.forEach(FR => {
        //get rows matching team name
        var rws = gsheetrows.filter(x => x.Team == FR.TeamName);
        // console.log('rows matching ', rws);
        //get distinct partidas
        var partidas = [...new Set(rws.map(item => item.Partida))];
        //add partidas to the Final result record
        partidas.forEach(p => {
          //get player Kills
          var rwsPartida = rws.filter(x => x.Partida == p);
          var pos = Number(rwsPartida[0].Posicion);
          var selected = rwsPartida[0].Selected == "si";
          var players = [];
          //add players
          rwsPartida.forEach(rp => {
            players.push({ UserName: rp.Jugador, Kills: rp.Kills });
          });

          FR.Matches.push({
            Id: FR.TeamName + p,
            MatchNumber: p,
            Players: players,
            Placement: pos,
            Selected: selected
          });
        });
      });

      return FinalResult;
    },

    GetTeamMatches: function() {
      var tm = this.FinalResults.filter(x => x.Id == this.SelectedTeam.Id)[0];

      this.TeamScore = tm.TeamTotalPoints;
      this.TeamMatches =
        tm == undefined ? {} : tm.Matches.filter(x => x.Selected);
    },
    DecodeFormat(frmt,vl)
    {
      console.log('decode', frmt, vl);
      switch(frmt)
      {
        case "Number": return Number(vl);
        case "JSON": return JSON.parse(vl);


      }

    },

    SetVariables: function(jsonVal){
          this.LocalSettings= JSON.parse(jsonVal);

    },


  }
});
</script>
<style scoped>
.hiddenRow {
  padding: 0 4px !important;
  background-color: #0e0c03;
  font-size: 13px;
  color: #fff;
}
.accordian-body span {
  color: #a2a2a2 !important;
}
.page-section {
  padding: 0;
}
@media (min-width: 768px) {
  section {
    padding: 0;
  }
}

.ProgressText {
  font-size: 1.3em;
  font-weight: 600;
}
</style>

