<template>
  <section class="page-sectionx" style="background-color:#fff;" id>
    <div class="container">
      <div class="row">
        <div class="col-md-4">
          <h1>Inicio de Teams</h1>
        </div>
      </div>

      <div class="container">
        <div class="row col custyle">
          <table class="table table-striped custab">
            <thead>
              <tr>
                <th>Team</th>
                <th>Capitan</th>
                <th>fecha de Inicio</th>
                <th>Empezar</th>
              </tr>
            </thead>
            <tr v-for="(item, index) in this.Teams" v-bind:key="item.id">
              <td>{{item.teamName}}</td>
              <td>{{item.teamMembers.find(e => e.captain ==true).displayName }}</td>
              <td>{{(!item.gameStartTime)?"-Pendinete":FormaDate(item.gameStartTime)}}</td>

              <td>
                <button
                  class="btn btn-outline-success my-2 my-sm-0"
                  v-if="item.gameStartTime ==null"
                  v-on:click.prevent="Test(item.teamName,item.id)"
                >Empezar</button>
              </td>
            </tr>
          </table>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
const axios = require("axios").default;
import Vue from "vue";
import { myVar, Settings } from "@/variables.js";
var moment = require("moment");

export default Vue.extend({
  data() {
    return {
      Teams: [],
      DateFormat: {
        month: "2-digit",
        day: "2-digit",
        hour: "numeric",
        minute: "numeric"
      }
    };
  },
  mounted: function() {
    // this.Authx();
    this.GetTeams();
  },
  methods: {
    FormaDate(strDate) {
      var dt = new Date(strDate);
      return moment(dt).format("M/D hh:mma");
    },

    Authx: function() {
      var decrypted = CryptoJS.AES.decrypt(Settings.pt, "ZUVvShFSES2");
      var pass = prompt("pass");
      if (decrypted.toString(CryptoJS.enc.Utf8) != pass) {
        this.$router.push("/");
      }
    },

    Test: function(teamName, TeamId) {
      var r = confirm("Empezar ? " + teamName);

      if (!r) {
        return;
      }

      var success = this.UpdateTeamStartTime(teamName);
    },

    UpdateTeamStartTime(teamName) {
      var endpoint = Settings.API + "/teams";
      var that = this;
      axios
        .patch(endpoint, {
          teamName: teamName
        })
        .then(function(response) {
          //Success
          alert("Actualizado");

          that.GetTeams();
        })
        .catch(function(error) {
          console.log("ERROR ", error);
          alert("Error");
        });
    },
    GetTeams: function() {
      console.log("Getting teams");
      var endpoint = Settings.API + "/teams";
      axios
        //.get(endpoint,{headers:{'Access-Control-Allow-Origin': '*' }})
        .get(endpoint)

        .then(response => {
          // handle success
          console.log(response);
          this.Teams = response.data;
        })
        .catch(error => {
          // handle error
          console.log(error);
        })
        .finally(function() {
          // always executed
        });

      //
    }
  }
});
</script>
